import { ColumnDef, Row } from '@tanstack/react-table';
import { useNavigate } from 'react-router-dom';
import { Customer } from '@/types/customer.types';
import ViewIcon from '@/components/icons/ViewIcon';

// Create a separate component for the actions cell
const ActionsCell = ({ row }: { row: Row<Customer> }) => {
    const navigate = useNavigate();

    return (
        <div className="me-6 text-end">
            <button
                onClick={() => navigate(`/customer-relations/view-customer/${row.original.id}`)}
                className="text-primary hover:text-primary/80"
            >
                <ViewIcon className="text-primery-blue-400" />
            </button>
        </div>
    );
};

export const columns: ColumnDef<Customer>[] = [
    {
        accessorKey: 'id',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    ID
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                {row.original.id}
            </div>
        ),
    },
    {
        accessorKey: 'name',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    اسم العميل
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                {row.original.full_name}
            </div>
        ),
    },
    {
        accessorKey: 'status',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    رقم الهاتف
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    {row.original.phone}
                </div>
            );
        },
    },
    {
        accessorKey: 'date',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    العنوان
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    {row.original.address}
                </div>
            );
        },
    },
    {
        accessorKey: 'service',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    البطاقة الشخصية
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    {row.original.personal_id_number}
                </div>
            );
        },
    },
    {
        accessorKey: 'location',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    رخصة القيادة
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    {row.original.driver_license_number}
                </div>
            );
        },
    },
    {
        accessorKey: 'location',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    عدد ايام الايجار
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    {row.original?.rent_days}
                </div>
            );
        },
    },
    {
        id: 'actions',
        header: () => {
            return (
                <div className="text-sm text-center px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    مشاهدة
                </div>
            );
        },
        cell: ({ row }) => <ActionsCell row={row} />,
    },
];
