import UnderlineHeading from '@/components/heading/UnderlineHeading';
import { CustomSelect } from '@/components/dropDown/Dropdown';
import MultipleBarChart from '@/components/charts/MultipleBarChart';
import { NewCustomersStats } from '../../types/newUsersStats.types';
import { ChartConfig } from '@/components/ui/chart';

interface Props {
    stats: NewCustomersStats | null;
    setCustomersStatsPeriod: (period: string) => void;
    customersStatsPeriod: string;
}

const DemographicData = ({ stats, customersStatsPeriod, setCustomersStatsPeriod }: Props) => {
    let DemographicChartData: any[] = [];

    if (stats?.new_customers) {
        DemographicChartData = Object.entries(stats?.new_customers).map(([key, value]) => ({
            days: key,
            visitors: value,
        }));
    }

    const DemographicChartConfig = {
        visitors: {
            label: 'عدد العملاء',
            color: 'hsl(var(--green-success-100))',
        },
    } satisfies ChartConfig;

    return (
        <div className="shadow-[0px_2px_12px_0px_#14142B14] rounded-[0.5rem]">
            <div className="px-4 pt-4 flex flex-row justify-between mb-10">
                <UnderlineHeading text="عدد العملاء" />
                <CustomSelect
                    options={[
                        { label: 'يومي', value: 'daily' },
                        { label: 'أسبوعي ', value: 'weekly ' },
                        { label: 'شهري', value: 'monthly' },
                        // { label: 'سنوي', value: 'سنوي' },
                    ]}
                    value={customersStatsPeriod}
                    onValueChange={setCustomersStatsPeriod}
                    placeholder="القائمة"
                    width="min-w-[101px]"
                />
            </div>
            <div className="ps-6 pb-4 flex flex-row items-center justify-center gap-6">
                <MultipleBarChart
                    chartConfig={DemographicChartConfig}
                    chartData={DemographicChartData}
                    // barLabelColor="#fff"
                />
            </div>
        </div>
    );
};

export default DemographicData;


