import UnderlineHeading from '@/components/heading/UnderlineHeading';
import { CustomSelect } from '@/components/dropDown/Dropdown';
import { useState } from 'react';
import MultipleBarChart from '@/components/charts/MultipleBarChart';
import { FavCarsChartConfig, FavCarsChartData } from '../../data/chartsData';

const FavCarsData = () => {
    const [selectedValue, setSelectedValue] = useState('');

    return (
        <div className="shadow-[0px_2px_12px_0px_#14142B14] rounded-[0.5rem]">
            <div className="px-4 pt-4 flex flex-row justify-between mb-10">
                <UnderlineHeading text="السيارات المفضلة" />
                <CustomSelect
                    options={[
                        { label: 'يومي', value: 'يومي' },
                        { label: 'أسبوعي ', value: 'أسبوعي ' },
                        { label: 'شهري', value: 'شهري' },
                        { label: 'سنوي', value: 'سنوي' },
                    ]}
                    value={selectedValue}
                    onValueChange={setSelectedValue}
                    placeholder="القائمة"
                    width="min-w-[101px]"
                />
            </div>
            <div className="ps-6 pb-4 flex flex-row items-center justify-center gap-6">
                <MultipleBarChart chartConfig={FavCarsChartConfig} chartData={FavCarsChartData} barLabelColor="#fff" />
            </div>
        </div>
    );
};

export default FavCarsData;
