import { useEffect, useState } from 'react';
import DemographicData from './DemographicData';
import FavCarsData from './FavCarsData';
import { apiGet } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';
import { NewCustomersStats } from '../../types/newUsersStats.types';

export default function ChartsComponent() {
    const [customersStats, setCustomersStats] = useState<NewCustomersStats | null>(null);
    const [customersStatsPeriod, setCustomersStatsPeriod] = useState('daily');

    useEffect(() => {
        const fetchStats = async () => {
            try {
                const res = await apiGet({
                    endpoint: endpoints.customer.dashboardNewCustomers,
                    queryParams: { period: customersStatsPeriod },
                });
                setCustomersStats(res.data.data);
            } catch (err) {
                setCustomersStats(null);
            }
        };
        fetchStats();
    }, [customersStatsPeriod]);

    return (
        <div className="mt-6 grid custom-grid-big gap-6 justify-center sm:justify-between">
            <DemographicData
                stats={customersStats}
                customersStatsPeriod={customersStatsPeriod}
                setCustomersStatsPeriod={setCustomersStatsPeriod}
            />
            <FavCarsData />
        </div>
    );
}
