import { Card } from '@/types/statistics';
import StatisticsCards from '@/components/cards/StatisticsCards';
import SettingsIcon from '@/components/icons/statistics/SettingIcon';
import UserIconH from '@/components/icons/statistics/UserIconH';
import UserAddIcon from '@/components/icons/statistics/UserAddIcon';
import ChartIcon from '@/components/icons/statistics/ChartIcon';
import { useEffect, useState } from 'react';
import { apiGet } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';
import { HumanResourcesStats } from '../../types/humanResourcesStats.types';

export default function StatisticsCardsComponent() {
    const [stats, setStats] = useState<HumanResourcesStats | null>(null);

    useEffect(() => {
        const fetchStats = async () => {
            try {
                const res = await apiGet({ endpoint: endpoints.customer.dashboard });
                setStats(res.data.data);
            } catch (err) {
                setStats(null);
            }
        };
        fetchStats();
    }, []);

    const cards: Card[] = [
        { text: 'اجمالي عدد العملاء', icon: <UserIconH />, num: stats?.customer_count, percent: 0 },
        { text: 'العملاء الجدد', icon: <UserAddIcon />, num: stats?.new_customers, percent: 0 },
        { text: 'معدل تكرار الحجز', icon: <SettingsIcon />, num: 1, percent: 0 },
        { text: 'متوسط التقيم', icon: <ChartIcon />, num: stats?.rating_average, percent: 0 },
    ];
    return <StatisticsCards cards={cards} />;
}
